
html, body {
  overflow: hidden;
  overscroll-behavior: none;
  /* background-color: orange; */
}

body::-webkit-scrollbar {
  display: none;

}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.row {
  box-sizing: content-box;
  display: flex;
  flex-direction: row;
  gap: 5%;
}
.col {
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
}

h1 {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 600;
}
h3 {
  font-family: "Calibre";
  font-size: 24px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
}
p {
  font-family: "Calibre";
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
  
}


