#navigation {
  position: fixed;
  width: 100%;
  height: 20%;
  /* background: red; */
  z-index: 1000;
}

.navWrap {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
  height: 100%;
  padding-left: 6%;
  padding-right: 6%;
}
.leftNav {
  height: 100%;
  float: left;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.rightNav {
  height: 100%;
  float: right;
  display: flex;
  flex-direction: row;
  align-items: center;
}
#logo {
  position: absolute;
  font-size: 22px;
  font-weight: 600;
  /* text-transform: uppercase; */
  color: white;
}
.back-btn {
  position: absolute;
  font-size: 22px;
  font-weight: 300;
  /* text-transform: uppercase; */
  color: white;
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.menu-btn {
  color: white;
}
.navItem {
  font-size: 22px;
  font-weight: 300;
  color: white;
}
:link {
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  #navigation {
    width: 100%;
    height: 90px;
  }

  .navWrap {
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
    padding: 0 32px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  #logo {
    font-size: 16px;
    font-weight: 600;
  }

  .navItem {
    font-size: 16px;
    font-weight: 300;
    color: white;
  }
}
